var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("暴风女")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        野兽（亨利菲利普“汉克”麦考伊）是一部虚构的超级英雄，出现在由漫威漫画公司出版的美国漫画书中，是X战警的创始成员。这个角色最初被称为“野兽”，它是一种突变体，具有类似猿类的超人体力和敏捷性，超大的手脚，天才级别的智力，以及其他正常的外表和言语。最终被简称为“野兽”，汉克麦考伊经历了渐进的生理转变，永久地获得了动物的物理特征。这些包括蓝色皮毛，猿猴和猫的面部特征，尖耳朵，尖牙和爪子。野兽的体力和感觉增加到更高的水平。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        尽管汉克麦考伊的不人道的外观，他被描绘成在艺术和科学辉煌的，受过良好教育的人，他的诙谐幽默已知的，典型使用带刺的俏皮话与长词和智力引用分散他的敌人。他是生物化学和遗传学的世界权威，X战警的医学博士，以及Xavier研究所（X战警总部和年轻突变体学校）的科学和数学讲师。他也是一名突变的政治活动家，反对社会的偏见和对突变体的歧视。在与自己的兽性本能和对社会拒绝的恐惧作斗争的同时，Beast将他的身心礼物奉献给为人类和突变体创造一个更美好的世界。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        作为最初的X战警之一，Beast自首次亮相以来就经常出现在与X战警相关的漫画中。他也是复仇者和后卫的成员。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        由作家斯坦李和艺术家/合作作家杰克柯比创作的角色首次出现在X战警＃1（1963年9月）。斯坦李在前言中写道：X战警：终极指南，他让野兽最清晰，最有说服力，并且很好地阅读了X战警，与他的野蛮外表形成鲜明对比。此外，该书认为Werner Roth-Roy Thomas团队因其“原始X战警的吸引力和敏感特征”而获得钦佩。罗斯，别名杰伊加文，已经完全接受柯比的问题＃18，托马斯是一个新的天才。为了吸引新读者，Beast获得了个性化的，色彩缤纷的新服装，以及第39期的其他X战警。在Jim Steranko任职期间，加入了“令人兴奋的艺术”，Roth回归，与Neal Adams合作，将Kirby的风格与“现实主义，理想化的美丽和史诗般的宏伟”融为一体。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span',[_vm._v(" Ultimate Fallout #4 (August 2011)")